<template>
  <v-container pb-0 px-0 pt-5 fluid>
    <v-row no-gutters align="center" justify="center">
      <v-col cols="11">
        <v-menu
          v-model="datePickerMenu"
          :close-on-content-click="false"
          transition="scale-transition"
          offset-y
          max-width="290px"
          min-width="290px"
        >
          <template v-slot:activator="{ on }">
            <v-text-field
              v-model="userDate"
              color="indigo"
              outlined
              label="Date:"
              readonly
              append-icon="mdi-calendar-edit"
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker color="indigo" header-color="indigo" @input="submit" v-model="date"></v-date-picker>
        </v-menu>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import moment from "moment";

export default {
  name: "date-picker",
  //implement user can only chose current date or greater no past dates
  data() {
    return {
      date: moment().format("YYYY-MM-DD"),
      userDate: moment().format("L"),
      datePickerMenu: false
    };
  },
  methods: {
    submit() {
      this.userDate = moment(this.date).format("L");
      this.datePickerMenu = !this.datePickerMenu;
    },
    close() {
      this.datePickerMenu = false;
    }
  },
  computed: {}
};
</script>

