<template>
  <v-container pa-0 fluid>
    <v-row no-gutters align="center" justify="center">
      <v-col cols="11">
        <v-menu
          v-model="timePickerMenu"
          :close-on-content-click="false"
          transition="scale-transition"
          offset-y
          max-width="290px"
          min-width="290px"
        >
          <template v-slot:activator="{ on }">
            <v-text-field
              v-model="userTime"
              color="indigo"
              label="Time:"
              outlined
              readonly
              append-icon="mdi-clock-outline"
              v-on="on"
            ></v-text-field>
          </template>

          <v-time-picker
            v-model="time"
            @click:minute="submit"
            color="indigo"
            header-color="indigo"
            format="ampm"
            ampm-in-title
          ></v-time-picker>
        </v-menu>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import moment from "moment";

export default {
  name: "time-picker",
  data() {
    return {
      time: "",
      userTime: moment().format("LT"),

      timePickerMenu: false
    };
  },
  methods: {
    getInput(time) {
      const timeArray = time.split(":");
      const joinedArray = timeArray.join("");
      return moment(joinedArray, "hmm").format("h:mm A");
    },
    submit() {
      this.userTime = this.getInput(this.time);
      this.timePickerMenu = !this.timePickerMenu;
    },
    close() {
      this.timePickerMenu = !this.timePickerMenu;
    }
  }
};
</script>

