<template>
  <v-dialog dark v-model="contactUsMenu" persistent max-width="600px">
    <template v-slot:activator="{ on }">
      <v-btn min-width="130" large max-width="180" v-on="on" light outlined color="white">Contact Us</v-btn>
    </template>

    <v-card light>
      <v-card-title>Contact Us</v-card-title>

      <v-divider></v-divider>

      <v-card-text class="mt-10">
        <v-form ref="contact" v-model="valid">
          <v-row>
            <v-col cols="12" md="6">
              <v-text-field
                :rules="firstNameRules"
                color="indigo"
                outlined
                label="First Name"
                placeholder="Joe"
                v-model="firstName"
              ></v-text-field>
            </v-col>
            <v-col cols="12" md="6">
              <v-text-field
                :rules="lastNameRules"
                color="indigo"
                outlined
                label="Last Name"
                placeholder="Smith"
                v-model="lastName"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-text-field
            :rules="emailRules"
            color="indigo"
            outlined
            placeholder="email@example.com"
            label="Email"
          ></v-text-field>
          <v-text-field
            :rules="phoneRules"
            color="indigo"
            outlined
            label="Phone"
            placeholder="123-123-1234"
          ></v-text-field>
          <v-textarea
            :rules="messageRules"
            color="indigo"
            counter="255"
            prepend-inner-icon="mdi-comment-edit"
            auto-grow
            filled
            clearable
            clear-icon="mdi-close-circle"
            label="Message"
            placeholder="Send Us A Message!"
          ></v-textarea>
        </v-form>
      </v-card-text>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn rounded color="red" outlined @click="close">Cancel</v-btn>
        <v-btn
          :dark="valid"
          :disabled="!valid"
          @click="submit"
          class="white--text"
          rounded
          color="indigo"
        >Submit</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "contact-us",
  data() {
    return {
      firstName: "",
      lastName: "",
      email: "",
      phone: "",
      message: "",
      firstNameRules: [v => !!v | "First Name is required"],
      lastNameRules: [],
      emailRules: [],
      phoneRules: [],
      messageRules: [],
      valid: true,
      contactUsMenu: false
    };
  },
  methods: {
    submit() {
      this.$refs.contact.validate();
    },
    close() {
      this.$refs.contact.reset();
      this.contactUsMenu = false;
    }
  }
};
</script>

<style></style>
